@font-face {
  font-family: stretchPro;
  src: url('./assets/fonts/Stretch Pro V2 - STRETCH.ttf');
}
@font-face {
  font-family: stretchProBasic;
  src: url('./assets/fonts/Stretch Pro V2 - BASIC.ttf');
}
@font-face {
  font-family: eurostylebold;
  src: url('./assets/fonts/EurostileBold.ttf');
}
@font-face {
  font-family: eurostyleextended;
  src: url('./assets/fonts/EurostileExtended.ttf');
}
@font-face {
  font-family: eurostyle;
  src: url('./assets/fonts/EuroStyle Normal.ttf');
}
@font-face {
  font-family: 'cooperhewitt';
  src: local('cooperhewitt'), url('./assets/font/CooperHewitt-Book.otf') format('truetype');
}
@font-face {
  font-family: 'cooperhewittbold';
  src: local('cooperhewittbold'), url('./assets/font/CooperHewitt-Bold.otf') format('truetype');
}

body,
html,
h1,
h2,
h3,
h4,
p
{
  margin:0;
  font-family: 'cooperhewitt';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'cooperhewitbold';
}
html,
body {
  overflow-y: auto;
  height: 100%;
}

body {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-position: center center !important;
  background-attachment: fixed !important;
  background-size: cover !important;
  margin:0;

}

.MuiButton-containedPrimary,
.MuiButton-containedPrimary:hover,
.MuiListItem-button:hover,
.MuiButtonBase-root:hover {
  color: #fff !important;
}

code {
  font-family: 'cooperhewitt';
}

