.bnbmlogo {
  font-family: 'stretchPro' !important;
  color: white;
  text-decoration: none;
  font-size: '35px' !important;
}
.navLink {
  color: white !important;
  font-size: '12px' !important;
  font-family: 'cooperhewittbold';
}

.navLink:hover {
  filter: drop-shadow(0 0 1rem #ffff00);
}


.account {
  display: flex;
  align-items: center;
}

.account > img,
.account > div {
  margin-right: 8px;
}


.navTokenPrice {
  padding-top: 5px;
  padding-right: 15px;
  color: #fff;
  font-family: 'cooperhewitt';
}

.navTokenIcon {
  height: 30px !important;
  width: 30px !important;
  padding-right: 12px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  overflow: hidden;
  font-family: 'cooperhewitt';
}

.bnbm {
  background-image: url('/bnbm.png');
}

.sbnb {
  background-image: url('/sbnb.png');
}

.bnb {
  background-image: url('/bnb.png');
}
